import proxy from './proxy.js';

export default (callback, config = {}, dconfig = {}) => {
  let url = 'http://gitee.com/lantu/mc-sdk-insight-public/raw/v2.0.1/mc-sdk.2.0.1.js'; // 
  var script = document.createElement("script");
  script.src = url;
  document.head.appendChild(script);
  script.onload = () => {
    var appkey = "24e1aeb8";
    MCK.auth(appkey);
    MCK.ready(()=>{
        // , API
        //  sdk  MCK
        // MCK === sdk, true
        var mobileSDK = {};
        proxy(MCK, mobileSDK, config);
        global.BH_MIXIN_SDK = MCK;
        callback({
            type: 'lt-success',
            sdk: mobileSDK
        });
    
      });

  };
}



